<template>
  <v-app>
    <v-container align="center">
      <div class="row">
        <v-btn
          type="button"
          class="btn btn-success btn-sm"
          v-b-modal.job-modal
          @click="addJob"
        >Add Job</v-btn>
        <v-btn
          type="button"
          class="btn btn-danger btn-sm"
          v-b-modal.month-modal
          @click="getTimeSheetsApproved"
        >View All Unpaid</v-btn>
        <v-container>
          <div class="parent">
            <div class="col">
              <v-combobox v-model="search.lead" label="Project Lead" :items="employees" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Client" label="Client" :items="clients" />
            </div>
            <div class="col">
              <v-combobox v-model="search.status" multiple label="Status" :items="status" />
            </div>
            <div class="col">
              <v-menu
                ref="menu"
                v-model="menu1"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="search.Start"
                    label="Start"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  first-day-of-week="1"
                  ref="picker"
                  dark
                  v-model="search.Start"
                  @change="change"
                ></v-date-picker>
              </v-menu>
            </div>
            <div class="col">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="search.End"
                    label="End"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  first-day-of-week="1"
                  ref="picker"
                  dark
                  v-model="search.End"
                  @change="change"
                ></v-date-picker>
              </v-menu>
            </div>
          </div>
        </v-container>
          <div class='pointer'>
        <table class="table table-hover">
          <thead>
            <tr>
			  <th  @click='sort("Job","Client")' scope="col" >Client</th>
              <th  @click='sort("Job","Name")' scope="col" >Job Name</th>
              <th  @click='sort("Summary","ActHours")' scope="col" >Hours</th>
              <th  @click='sort("Summary","Profit")' scope="col" >Net Position</th>
              <th  @click='sort("Summary","Profit2")' scope="col" >Est Current Profit</th>
              <th @click='sort("Summary","EstProfit")' scope="col" >Est Profit</th>
              <th @click='sort("Job","Project_Value")' scope="col" >PO Value</th>
              <th @click='sort("Summary","Invoiced")' scope="col" >Invoiced</th>
              <th @click='sort("Summary","Est")' scope="col" >Expected Cost</th>
              <th @click='sort("Summary","Act")' scope="col" >Actual Cost</th>
		  </tr>
          </thead>
          <tbody>
            <tr v-for="(job, index) in jobs" :key="index">
              <td @click="getJobDetails(job)">{{ job.Job.Client }}</td>
              <td @click="getJobDetails(job)">{{ job.Job.Name }}</td>
              <td @click="getJobDetails(job)">{{ job.Summary.ActHours }}/{{ job.Summary.Budget }}</td>
              <td @click="getJobDetails(job)">${{ job.Summary.Profit | formatNumber}}</td>
              <td @click="getJobDetails(job)">${{ job.Summary.Profit2 | formatNumber}}</td>
              <td @click="getJobDetails(job)">${{ job.Summary.EstProfit | formatNumber}}</td>
              <td @click="getJobDetails(job)">${{ job.Job.Project_Value | formatNumber}}</td>
              <td @click="getJobDetails(job)">${{ job.Summary.Invoiced | formatNumber}}</td>
              <td @click="getJobDetails(job)">${{ job.Summary.Est | formatNumber}}</td>
              <td @click="getJobDetails(job)">${{ job.Summary.Act | formatNumber}}</td>
            </tr>
          </tbody>
        </table>
          </div>  
        <table class="table table-hover">
          <thead>
            <tr>
              <th scope="col" ></th>
              <th scope="col" ></th>
              <th scope="col" ></th>
              <th scope="col" ></th>
              <th scope="col" ></th>
              <th scope="col" ></th>
              <th scope="col" ></th>
              <th scope="col" ></th>
              <th scope="col" ></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Totals:</td>
              <td></td>
              <td>{{ totals.Actual | formatNumber}}/{{totals.Budget | formatNumber}}</td>
              <td>${{ totals.Profit | formatNumber}}</td>
              <td>${{ totals.Profit2 | formatNumber}}</td>
              <td>${{ totals.Est_Profit | formatNumber}}</td>
              <td>${{ totals.Value | formatNumber}}</td>
              <td>${{ totals.Invoiced | formatNumber}}</td>
              <td>${{ totals.Expected | formatNumber}}</td>
              <td>${{ totals.Cost | formatNumber}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </v-container>
    <v-dialog ref="dialog" v-model="view" max-width="1200px">
      <v-card>
        <div>
          <JobForm ref="jobFrom" @submit="view=false; show=true;" />
        </div>
      </v-card>
    </v-dialog>
    <v-dialog ref="dialog2" v-model="view2" max-width="500">
      <v-card>
        <div>
          <Activity />
        </div>
      </v-card>
    </v-dialog>
    <v-snackbar :bottom="true" :left="true" v-model="show" color="success" dark>
      <v-icon color="white" class="mr-3">mdi-bell-plus</v-icon>

      <div>Submitted</div>

      <v-icon size="16" @click="show = false">mdi-close-circle</v-icon>
    </v-snackbar>
  </v-app>
</template>

<script>
import Axios from "axios";
import JobForm from "@/components/JobForm.vue";
import Activity from "@/components/Activity.vue";
export default {
  components: {
    JobForm,
    Activity
  },

  computed: {
    jobs() {
      let list = this.$store.getters.getJobs;
      if (this.search.lead != "" && this.search.lead != null) {
        list = list.filter(
          item => item.Job.Lead.value == this.search.lead.value
        );
      }
      if (this.search.status != "" && this.search.status != null) {
        var tempList = []
        for (var i = 0; i < this.search.status.length; i++) {
          var temp = [...list]
          temp = list.filter(
            item => item.Job.Status.value == this.search.status[i].value
          );
          tempList = tempList.concat(temp);
        }
        list = tempList
      }
      

      if (this.search.Start !== "" && this.search.End !== "") {
        list = list.filter(
          item => new Date(item.Job.Complete_Date) > new Date(this.search.Start)
        );
        list = list.filter(
          item => new Date(item.Job.Complete_Date) < new Date(this.search.End)
        );
      }

      if (this.search.Client !== null) {
      	list = list.filter(item => item.Job.Client.includes(this.search.Client));
    }  
	return list.sort((a,b) => {
      let modifier = 1;
      if(this.currentSortDir === 'desc') modifier = -1;
      if(a[this.currentSortPrime][this.currentSort] < b[this.currentSortPrime][this.currentSort]) return -1 * modifier;
      if(a[this.currentSortPrime][this.currentSort] > b[this.currentSortPrime][this.currentSort]) return 1 * modifier;
      return 0;
    });
    },
    totals() {
      var totals = {
        Profit: 0,
        Cost: 0,
        Invoiced: 0,
        Expected: 0,
        Value: 0,
        Budget: 0,
        Actual: 0,
        Position: 0,
        Est_Profit: 0,
		Profit2: 0
      };
      for (var i = 0; i < this.jobs.length; i = i + 1) {
        totals.Profit += this.jobs[i].Summary.Profit;
        totals.Profit2 += this.jobs[i].Summary.Profit2;
        totals.Invoiced += this.jobs[i].Summary.Invoiced;
        totals.Cost += this.jobs[i].Summary.Act;
        totals.Expected += this.jobs[i].Job.Est_Project_Cost;
        totals.Value += this.jobs[i].Job.Project_Value;
        totals.Budget += this.jobs[i].Summary.Budget;
        totals.Actual += this.jobs[i].Summary.ActHours;
        totals.Est_Profit += this.jobs[i].Job.Project_Value - this.jobs[i].Job.Est_Project_Cost;

      }
      return totals;
    },
    employees() {
      return this.$store.getters.getEmployeeList;
    },
    clients () {
      let jobs = this.$store.getters.getJobs;
      var clients = []
      for(var i = 0; i < jobs.length; i++) {
        clients.push(jobs[i].Job.Client)
      }
      return clients

    }
	
  },

  data() {
    return {
      show: false,
      menu1: false,
      menu2: false,
      view: false,
      view2: false,
      active: null,
      search: {
        lead: "",
        Client: "",
        status: "",
        Start: "",
        End: ""
      },
      currentSort: 'Client',
      currentSortPrime: 'Job',
      currentSortDir: 'asc',
      status: [
        { text: "Planning", value: 0 },
        { text: "Active", value: 1 },
        { text: "Closed", value: 2 },
        { text: "Deleted", value: 3 }
      ]
    };
  },
  methods: {
    getJobDetails(job) {
      this.$refs.jobFrom.setTab(0);
      this.$store.commit("changeJob", job.Job.ID);
      this.view = true;
    },
    addJob() {
      this.$refs.jobFrom.setTab(1);
      this.$store.commit("addJob");
      this.view = true;
    },

    getTimeSheetsApproved() {
      this.view2 = true;
    },
    change() {
      this.menu1 = false;
      this.menu2 = false;
    },
    sort(s,d) {
    if(d === this.currentSort) {
      this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
    }
    this.currentSort = d;
    this.currentSortPrime = s;
    },
  }
};
</script>




<style>
.parent {
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
  padding: 5px;
  justify-content: center;
  background-color: rgb(250, 250, 250);
  display: flex;
}

.col {
  width: 400;
  margin: 1px;
}
.colB {
  width: 50px;
  margin: 1px;
}

.pointer { cursor: pointer; }

</style>
