<template>
  <v-app>
    <v-container align="center">
      <div class="row">
        <v-container>
          <div class="parent">
            <div class="col">
              <v-combobox v-model="search.Employee_Name" label="Employee" :items="employees" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Client" label="Client" :items="clients" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Job" multiple label="Job" :items="jobs" />
            </div>
            <div class="col">
              <v-menu
                ref="menu"
                v-model="menu1"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="search.Start"
                    label="Start"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  first-day-of-week="1"
                  ref="picker"
                  dark
                  v-model="search.Start"
                  @change="change"
                ></v-date-picker>
              </v-menu>
            </div>
            <div class="col">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="search.End"
                    label="End"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  first-day-of-week="1"
                  ref="picker"
                  dark
                  v-model="search.End"
                  @change="change"
                ></v-date-picker>
              </v-menu>
            </div>
          </div>
        </v-container>
		<div class='pointer'>
        <table class="table table-hover">
          <thead>
            <tr>
              <th @click="sort('Employee_Name')" scope="col" >Employee</th>
              <th @click="sort('Date')" scope="col" >Date</th>
              <th @click="sort('Client')" scope="col" >Client</th>
              <th @click="sort('Job_Name')" scope="col" >Job Name</th>
              <th @click="sort('Hours')" scope="col" >Hours</th>
              <th @click="sort('Paid')" scope="col" >Paid</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(hour, index) in hours" :key="index" >
              <td @click='getTimeSheet(hour)'>{{ hour.Employee_Name }}</td>
              <td @click='getTimeSheet(hour)'>{{ hour.Date }}</td>
              <td @click='getTimeSheet(hour)'>{{ hour.Client }}</td>
              <td @click='getTimeSheet(hour)'>{{ hour.Job_Name }}</td>
              <td @click='getTimeSheet(hour)'>{{ hour.Hours}}</td>
              <td>
               <v-checkbox type="checkbox" v-model="hour.Paid"></v-checkbox>
              </td>
            </tr>
          </tbody>
        </table>
		</div>
      </div>
      <v-btn @click='exportData'>Save Changes</v-btn>
    </v-container>
    <v-dialog ref="dialog" v-model="view" max-width="1200px" >
      <v-card height='300px' >
        <div>
          <TimeSheetEdit ref="timesheet" @saved="view=false; show=true;" />
        </div>
      </v-card>
    </v-dialog>
    <v-snackbar :bottom="true" :left="true" v-model="show" color="success" dark>
      <v-icon color="white" class="mr-3">mdi-bell-plus</v-icon>

      <div>Submitted</div>

      <v-icon size="16" @click="show = false">mdi-close-circle</v-icon>
    </v-snackbar>
  </v-app>
</template>

<script>
import Axios from "axios";
import TimeSheetEdit from '@/components/TimeSheetEdit.vue'
export default {
  components: {
    TimeSheetEdit
  },

  computed: {
    hours() {
      let list = this.$store.getters.getAllHours;
      if (this.search.Employee_Name != "" && this.search.Employee_Name != null) {
        list = list.filter(
          item => item.Employee_Id == this.search.Employee_Name.value
        );
      }
      if (this.search.Job != "" && this.search.Job != null) {
        var tempList = []
        for (var i = 0; i < this.search.Job.length; i++) {
          var temp = [...list]
          temp = list.filter(
            item => item.Job_Name == this.search.Job[i]
          );
          tempList = tempList.concat(temp);
        }
        list = tempList
      }
      

      if (this.search.Start !== "" && this.search.End !== "") {
        list = list.filter(
          item => new Date(item.Date) > new Date(this.search.Start)
        );
        list = list.filter(
          item => new Date(item.Date) < new Date(this.search.End)
        );
      }

      if (this.search.Client !== null) {
      	list = list.filter(item => item.Client.includes(this.search.Client));
    }  
	return list.sort((a,b) => {
      let modifier = 1;
      if(this.currentSortDir === 'desc') modifier = -1;
      if(a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
      if(a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
      return 0;
    });
    },
    employees() {
      return this.$store.getters.getEmployeeList;
    },
    clients () {
      let jobs = this.$store.getters.getJobs;
      var clients = []
      for(var i = 0; i < jobs.length; i++) {
        clients.push(jobs[i].Job.Client)
      }
      return clients

    },
    jobs () {
      let jobsList = this.$store.getters.getJobs;
      var jobs = []
      for(var i = 0; i < jobsList.length; i++) {
        jobs.push(jobsList[i].Job.Name)
      }
      return jobs

    }
	
  },


  data() {
    return {
      view: false,
      show: false,
      active: null,
      search: {
        Employee_Name: "",
        Client: "",
        Job: "",
        Start: "",
        End: ""
      },
      currentSortDir: 'asc',
      currentSort:'Employee_Name',
    };
  },
  methods: {
    sort(s) {
    if(s === this.currentSort) {
      this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
    }
    this.currentSort = s;
    },
    exportData() {
    const path = 'http://13.55.48.180:5000/hours/update/all'
    const payload = this.hours
    Axios.post(path,payload)
        .then((res) => {
            this.$store.dispatch('getAllHours')
			this.show = true
        })
    },

    change() {
      this.menu1 = false;
      this.menu2 = false;
    },
    importHours() {
      this.$store.dispatch('getAllHours')
    },

	getTimeSheet(hour) {
        console.log(hour)
		this.$refs.timesheet.updateDate(hour.Date, hour.Employee_Id, hour.Job_Id)	
	    this.view = true;
    }
  }
};
</script>




